import React,{Component} from 'react';
import HeaderComponent from "../../components/HeaderComponent/index";
import Info from './subpage/Info'
import Comment from './subpage/Comment'
import Buy from "../../components/Buy/index";
import {connect} from 'react-redux';
import * as Actions from '../../actions/store';
import {bindActionCreators} from 'redux'
//this.propshistory,match....
class Detail extends Component{
    constructor(){
        super();
        this.state ={
            isStore:false
        }
    }
    render(){
        console.log(this);
        return (
            <div>
                {/**/}
                <HeaderComponent title="" history={this.props.history}/>
                {/**/}
                <Info id={this.props.match.params.id}/>
                {/**/}
                 <Buy buy={this.buy.bind(this)} store={this.store.bind(this)} isStore={this.state.isStore}/>
                {/**/}
                <Comment id={this.props.match.params.id}/>
            </div>
        )
    }
    componentDidMount(){
        //redux[],
        //id 
        let id = this.props.match.params.id;
       //some    true false
        let flag = this.props.store.some(item=>item===id)
        //
        if(flag){
            this.setState({
                isStore:flag
            })
        }
    }
    checkLogin(){
        //true false
        if(this.props.userInfo.username){
            return true
        }else {
            return false
        }

    }
    buy(){ //
        let flag = this.checkLogin();
        //
        if(flag){
            this.props.history.push('/user');
        }else{
            //  // /login/detail/123132   /login/asdasd
            this.props.history.push('/login/'+encodeURIComponent('/detail/'+this.props.match.params.id));
        }
        //....

    }
    store(){ //
        //
         let flag = this.checkLogin();
         if(!flag){  //
             this.props.history.push('/login/'+encodeURIComponent('/detail/'+this.props.match.params.id));
         }
         let id = this.props.match.params.id;
         if(this.state.isStore){
             //
             this.props.storeActions.remove(id);
         }
         else{
             //store 
             this.props.storeActions.add(id);
         }
         this.setState({
             isStore:!this.state.isStore
         })
    }
}
export default connect(
    state=>{
        return{
            userInfo:state.userInfo,
            store:state.store //
        }
    },
    dispatch=>{
        return{
            storeActions:bindActionCreators(Actions,dispatch)
        }

    }



)(Detail)


